:root {
  --black: #000000;
  --white: #ffffff;
  --light-blue: #81b3d2;
  --grey: #eeeeee;
  --brown: #766357;
  --text: #333333;
  --background: #f2f2f2;
  --background-shadow: rgba(0, 1, 1, 0.2);
  --form-shadow: rgba(0, 1, 1, 0.15);
  --footer: rgba(255, 255, 255, 0.9);
  --footer-html: #231f20;
  --light-blue-hover: #669ec0;
  --light-blue-active: #5496bd;
  --light-grey: #bdbbbc;
  --calendar: #a9a9a9;
  --black-active: rgba(0, 0, 0, 0.3);
  --black-hover: rgba(0, 0, 0, 0.6);
  --white-active: rgba(255, 255, 255, 0.3);
  --brown-hover: #604e43;
  --brown-active: #503e33;
  --form-hover: #ebebeb;
  --form-focus: #e5e5e5;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/pt-sans-v12-latin_cyrillic-regular.woff2") format("woff2"), url("../../fonts/pt-sans-v12-latin_cyrillic-regular.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../../fonts/pt-sans-v12-latin_cyrillic-700.woff2") format("woff2"), url("../../fonts/pt-sans-v12-latin_cyrillic-700.woff") format("woff");
}
.body {
  position: relative;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  background-color: var(--background);
  color: var(--black);
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 1200px;
  margin: 0 auto;
  background-color: var(--white);
  box-shadow: 0 5px 15px var(--background-shadow);
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.header__nav {
  position: relative;
  min-height: 56px;
  z-index: 100;
}
.header__nav::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 138px;
  height: 70px;
  background-image: url("../../img/svg/header__logo.svg");
  background-repeat: no-repeat;
}
.header__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  padding-left: 72px;
  padding-right: 72px;
  list-style-type: none;
}
.header__item {
  display: flex;
  align-items: center;
  width: 264px;
  font-weight: 700;
  text-transform: uppercase;
}
.header__item:nth-of-type(n),
.header__item:nth-of-type(2n) {
  justify-content: flex-start;
}
.header__item:nth-of-type(4n-1),
.header__item:nth-of-type(4n) {
  justify-content: flex-end;
}
.header__link {
  padding-top: 15px;
  padding-bottom: 15px;
  color: var(--black);
  transition: color 0.1s linear;
}
.header__link:hover {
  color: var(--light-blue);
}
.header__link:focus {
  color: var(--brown);
  opacity: 1;
}
.header__link:active {
  color: var(--black);
  opacity: 0.3;
}
.header__logo {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.greeting {
  position: relative;
  min-height: 509px;
  background-color: var(--light-blue);
  background-image: url("../../img/index/greeting__main-photo.jpg");
  background-size: 1200px 509px;
  background-repeat: no-repeat;
}
@media (min-resolution: 2dppx) {
  .greeting {
    background-image: url("../../img/index/greeting__main-photo@2x.jpg");
  }
}
.greeting__slogan {
  position: absolute;
  top: 77px;
  left: 0;
  right: 0;
  margin: 0 auto;
}
.greeting__element {
  position: absolute;
  bottom: 0;
  fill: var(--white);
}
.intro {
  padding-top: 40px;
  text-align: center;
}
.intro__title {
  width: 470px;
  margin: 0 auto 55px;
  font-weight: 700;
  font-size: 21px;
  text-transform: uppercase;
}
.intro__subtitle {
  text-transform: uppercase;
  margin-bottom: 55px;
  color: var(--text);
}
.features__item {
  display: flex;
  align-items: center;
}
.features__text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-grow: 1;
  box-sizing: border-box;
  width: 400px;
  padding-top: 27px;
  padding-bottom: 26px;
  padding-right: 36px;
  padding-left: 36px;
  text-align: center;
  color: var(--white);
  background-color: var(--light-blue);
}
.features__text--background {
  flex: 1 1 0;
  color: var(--black);
  background: var(--grey);
}
.features__suptitle {
  padding-left: 89px;
  padding-right: 89px;
  font-weight: 700;
  font-size: 21px;
  line-height: 21px;
  text-transform: uppercase;
}
.features__title {
  line-height: 21px;
}
.features__subtitle {
  line-height: 21px;
  text-transform: uppercase;
}
.features__picture {
  display: flex;
}
.features__image {
  width: 800px;
  height: 256px;
}
.features__included-list {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
}
.features__included-item {
  box-sizing: border-box;
  width: 400px;
  padding-top: 55px;
  padding-right: 50px;
  padding-bottom: 80px;
  padding-left: 50px;
}
.features__icon {
  fill: var(--light-blue);
}
.features__included-suptitle {
  font-weight: 700;
  font-size: 21px;
  line-height: 21px;
  text-transform: uppercase;
}
.features__included-title {
  line-height: 21px;
  text-transform: uppercase;
  color: var(--textF);
}
.features__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.registration {
  position: relative;
  padding-top: 40px;
  text-align: center;
}
.registration__title {
  width: 470px;
  margin: 0 auto 55px;
  font-weight: 700;
  font-size: 21px;
  text-transform: uppercase;
}
.registration__subtitle {
  width: 435px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 55px;
  text-transform: uppercase;
  text-align: center;
  color: var(--text);
}
.registration__button {
  width: 568px;
  box-sizing: border-box;
  padding-top: 31px;
  padding-bottom: 31px;
  padding-right: 60px;
  padding-left: 60px;
  font-size: 21px;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--brown);
  border: none;
  cursor: pointer;
  transition: background-color 0.1s linear;
}
.registration__button:hover {
  background-color: var(--brown-hover);
}
.registration__button:active {
  color: var(--white-active);
  background-color: var(--brown-active);
}
.registration__button--form {
  padding-top: 16px;
  padding-bottom: 16px;
  width: 100%;
  background-color: var(--light-blue);
}
.registration__button--form:hover {
  background-color: var(--light-blue-hover);
}
.registration__button--form:active {
  background-color: var(--light-blue-active);
}
.registration__form {
  position: absolute;
  top: 312px;
  left: 0;
  right: 0;
  z-index: 10;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 568px;
  padding-top: 55px;
  padding-bottom: 55px;
  padding-right: 55px;
  padding-left: 55px;
  margin: 0 auto;
  background-color: var(--white);
  box-shadow: 0 7px 15px var(--form-shadow);
  animation: bounce 0.6s;
}
.registration__form--hidden {
  display: none;
}
.registration__input {
  box-sizing: border-box;
  width: 346px;
  height: 38px;
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 13px;
  padding-right: 38px;
  font-weight: 700;
  text-transform: uppercase;
  border: none;
  background-color: var(--background);
  outline: none;
  transition: background-color 0.1s linear;
}
.registration__input:hover {
  background-color: var(--form-hover);
}
.registration__input:focus {
  border: 2px solid var(--form-focus);
  box-sizing: border-box;
  background-color: var(--blue);
}
.registration__input-wrapper {
  position: relative;
}
.registration__input--small {
  width: 114px;
  padding-right: 0;
  padding-left: 0;
  text-align: center;
  appearance: textfield;
}
.registration__plus {
  position: absolute;
  right: 0;
  padding-top: 14px;
  padding-right: 14px;
  padding-bottom: 13px;
  padding-left: 14px;
  fill: var(--calendar);
  opacity: 1;
  cursor: pointer;
}
.registration__plus:hover {
  fill: var(--black);
}
.registration__plus:active {
  fill: var(--light-blue);
}
.registration__minus {
  position: absolute;
  padding-top: 18px;
  padding-right: 13px;
  padding-bottom: 18px;
  padding-left: 13px;
  fill: var(--calendar);
  opacity: 1;
  cursor: pointer;
}
.registration__minus:hover {
  fill: var(--black);
}
.registration__minus:active {
  fill: var(--light-blue);
}
.registration__input--small::-webkit-outer-spin-button,
.registration__input--small::-webkit-inner-spin-button {
  appearance: none;
}
.registration__calendar {
  position: absolute;
  top: 7px;
  right: 9px;
  fill: var(--calendar);
  opacity: 1;
  cursor: pointer;
}
.registration__calendar:hover {
  fill: var(--black);
}
.registration__calendar:active {
  fill: var(--light-blue);
}
.registration__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}
.registration__wrapper:last-of-type {
  margin-bottom: 54px;
}
.registration__label {
  font-weight: 700;
  text-transform: uppercase;
}
.registration__map {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  min-height: 594px;
  border: none;
}
@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
.footer {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  min-height: 120px;
  background-color: var(--footer);
}
.footer__contact-list {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer__contact-item {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 21px;
  text-transform: uppercase;
  color: var(--black);
  transition: color 0.1s linear;
}
.footer__contact-item:hover {
  color: var(--black-hover);
}
.footer__contact-item:active {
  color: var(--black-active);
}
.footer__contact-item:nth-of-type(1) {
  margin-bottom: 9px;
}
.footer__social-list {
  display: flex;
  justify-content: space-between;
  width: 150px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 46px;
  height: 48px;
  background-color: var(--light-blue);
  transition: background-color 0.1s linear;
}
.footer__social-link:hover {
  background-color: var(--light-blue-hover);
}
.footer__social-link:active {
  background-color: var(--light-blue-active);
}
.footer__social-link:active .footer__icon {
  opacity: 0.3;
}
.footer__icon {
  fill: var(--white);
  opacity: 1;
}
.footer__copyright-link {
  display: flex;
  align-items: center;
}
.footer__copyright-text {
  margin-right: 8px;
  text-transform: uppercase;
  color: var(--black);
}
.footer__copyright-svg {
  fill: var(--footer-html);
}
.footer__copyright-link:hover .footer__copyright-svg {
  fill: var(--light-blue);
}
.footer__copyright-link:active .footer__copyright-svg {
  fill: var(--light-grey);
}
